.WhyDetBase,
.PersonalityClubs {
  justify-content: center;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
  background-color: var(--baseBlue);
}

.WhyDetBackground {
  width: 100%;
}

.WhyDetData {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: -webkit-fill-available;
  overflow: hidden;
}

.WhyDetText {
  width: 50%;
}

.WhyDetImages {
  width: 40%;
  display: flex;
  justify-content: flex-end;
}

.WhyDetImages img {
  width: 85%;
}

.WhyDetTitle {
  margin: 0;
  font-weight: 700;
  font-style: normal;
  font-size: var(--webHeading2);
  color: var(--baseWhite);
}

.WhyDetTitleOrng {
  color: var(--baseOrange);
}

.WhyDetIntroDiv {
  width: 80%;
  margin-top: 7%;
}

.WhyDetIntro {
  font-size: var(--webNormalFont);
  line-height: 1.4em;
  color: var(--baseWhite);
  font-weight: 300;
}

.WhyDetImages .button {
  display: none;
}

.WhyDetImagesRow {
  display: flex;
  justify-content: space-around;
  width: 100%;
  margin: 4% 0 5%;
}

.WhyDetImageCol {
  width: 25%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  transition: transform 0.2s;
}

.WhyDetImageCol:hover {
  transform: scale(1.1);
}

.WhyDetImageIcon {
  width: 3.8vw;
  margin-bottom: 1.2vw;
}

.WhyDetIconText {
  font-size: var(--webNormalFont2);
  color: var(--baseOrange);
  font-weight: 600;
  text-align: center;
}

.btn--whydetpage {
  padding: 0.3em 1em;
}

.personalityClubsMini {
  color: var(--shade2DullWhite);
  font-size: var(--webSmallFont);
  margin: 0 0 0.3em;
}

.PersonalityClubs li {
    margin: 1.3em 0;
}

.PersonalityClubs .WhyDetTitle {
    margin-bottom: 2em;
}