.navbar {
    box-shadow: 0px 2px 9px 3px rgba(66, 66, 66, 0.1);
    padding: 0.9em 0 0.7em 0.5em;
    position: fixed;
    opacity: 100;
    width: 100%;
    z-index: 999;
    background-color: var(--baseBlue);
}

.mobile {
    min-height: 3em;
    display: none;
}

.navbar-dark .navbar-toggler {
    color: var(--baseWhite);
}

.navbar-toggler:focus {
    box-shadow: none;
}

.navbar-dark .navbar-nav .nav-link {
    justify-content: center;
    display: flex;
}

.mobnav-content {
    margin: 1.5vh 0 1vh;
}

.navbar-collapse {
    margin-top: 1.5vh;
}

.twenemin {
    display: none;
}