.journey-page {
    background-color: var(--baseWhite);
    position: relative;
    display: flex;
    justify-content: center;
}

.journey-head {
    position: absolute;
    margin: 6vw 5% 0;
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.journey-heading {
    font-family: 'Poppins';
    font-weight: 700;
    font-size: var(--webHeading2);
}

.journey-steps {
    position: absolute;
    top: calc(3em + 15vw);
    width: 70%;
}

.journey-bg {
    width: 100%;
}

.sidebtn {
    margin-top: calc(0.1em + 2vw);
}