.predict-page {
  background-color: var(--baseWhite);
  position: relative;
  display: flex;
  justify-content: center;
}

.predict-head {
  position: absolute;
  margin: 5vw 5vw 0;
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.predict-title {
  color: var(--shade6Black);
  font-size: var(--webNormalFont);
  margin: 1em 0 1.5em;
}

input[type="range"] {
  -webkit-appearance: none !important;
  width: 60%;
  height: 3em;
  background: var(--baseOrange);
  border: none;
  outline: none;
  border-radius: 24px;
  margin-top: 3em;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none !important;
  width: 2.8em;
  height: 2.8em;
  background: var(--baseWhite);
  border: 2px solid var(--baseWhite);
  border-radius: 50%;
  cursor: pointer;
}

.range-values {
  width: 63%;
  display: flex;
  justify-content: space-between;
  margin-top: 1em;
}

.range-values h4 {
  font-size: var(--webNormalFont);
  transform: translate(22%, 22%);
}

.range-output {
  font-size: var(--webNormalFont4);
  margin-top: 3em;
}

.range-slider {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}
