div.card-container {
    width: 100%;
    justify-content: center;
}

div.experience-card {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: calc(100px + 20vw);
    padding-bottom: 2vw;
    width: 80vw;
    justify-content: space-evenly;
}

.testi-div {
    display: flex;
    flex-direction: column;
}

.passionbtn {
    font-size: 1vw;
    width: 100%;
    margin: 1vw 0 0 0;
    display: flex;
    justify-content: center;
}

.passionfire {
    height: calc(1em + 0.6vw);
    width: calc(1em + 0.8vw);
    padding-left: 0.5em;
}

div.testi-intro {
    margin: auto;
    width: calc(100px + 17vw);
    position: relative;
    background: linear-gradient(180deg, rgba(155, 155, 155, 0) 47.48%, rgba(0, 0, 0, 0.8) 100%);
    border-radius: 8px;
    overflow: hidden;
}

div.testi-intro img {
    width: calc(100px + 17vw);
}

div.testi-intro-text {
    display: flex;
    flex-direction: column;
    flex-grow: 3;
    justify-content: space-between;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: center;
}

div.testi-intro-text div.name {
    font-weight: 600;
    font-size: 1.3vw;
    color: var(--baseWhite);
}

div.testi-intro-text div.desc {
    font-family: 'Poppins';
    font-weight: 400;
    font-size: 1vw;
    color: var(--baseWhite);
}

div.sub-cards {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

div.sub-cards div.card {
    min-width: calc(100px + 20vw);
    white-space: pre;
    max-height: 30%;
    font-size: calc(0.4em + 0.5vw);
    align-items: center;
    padding: 1.5vh 1.1vw;
    box-shadow: 0px 0px 20px rgba(255, 219, 198, 0.5);
    border: none;
    border-radius: 4px;
}

div.sub-cards div.card div.sub-card-text h6 {
    color: var(--baseOrange);
    font-size: 1vw;
    font-weight: 600;
    margin-top: 0;
}

div.sub-cards div.ml-3 {
    margin-left: 3vw;
    margin-right: -3vw;
}

img.responsive-xp-icon {
    height: 3vw;
    margin-right: 1vw;
    width: auto;
}

button.react-multiple-carousel__arrow {
    background: none;
}

.react-multiple-carousel__arrow--right::before {
    color: gray;
}

.react-multiple-carousel__arrow--left::before {
    color: gray;
}

.react-multi-carousel-dot--active button {
    background: var(--baseOrange);
    border-color: var(--baseOrange);
}

.react-multi-carousel-dot button {
    border-color: rgba(128, 128, 128, 0.185);
    transition: background 0.3s;
}

.react-multiple-carousel__arrow::before {
    font-size: 30px;
    font-weight: 800;
}