/* section */
footer.page-footer {
    background: var(--shade2Blue);
    height: auto;
    display: flex;
    color: var(--dullWhite);
    font-family: 'Poppins';
    font-weight: 400;
    padding: 3vw 5vw;
}


footer.page-footer button {
    background: none;
    outline: none;
    border: none;
    text-align: left;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    font-size: var(--webSmallFont);
}

footer.page-footer button:hover {
    transform: scale(1.1);
    color: var(--baseWhite);
}

footer.page-footer img {
    width: calc(1em + 2vw);
    max-height: calc(1em + 1.5vw);
}

footer.page-footer .logo-foot img {
    width: 100%;
    max-height: fit-content;
}

.logo-foot {
    width: 53%;
    margin-bottom: 3vw;
}

footer.page-footer main.footer-content-box {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;
    margin-bottom: 1vh;
}

/* main content */
main.footer-content-box div {
    padding-top: 0.5vw;
    align-items: baseline;
}

main.footer-content-box h3 {
    font-family: 'Poppins';
    font-weight: 600;
    font-size: var(--webNormalFont3);
}

main.footer-content-box div.about-explorin {
    flex: 1 1 4vw;
    display: flex;
    flex-direction: column;
    width: 20vw;
    margin-right: 4vw;
    margin-left: 2vw;
}

main.footer-content-box div.explorin-empire {
    flex: 1 0 3vw;
    display: flex;
    flex-direction: column;
    width: 50%;
    margin-right: 1vw;
}

main.footer-content-box div.explorin-det {
    flex: 1 0 3vw;
    display: flex;
    flex-direction: column;
    margin-right: 1vw;
}

main.footer-content-box div.explorin-social {
    flex: 1 0 2vw;
    display: flex;
    flex-direction: column;
    margin-right: 1vw;
    align-items: stretch;
}

main.footer-content-box div.explorin-benefits {
    flex: 1 0 2vw;
    display: flex;
    flex-direction: column;
}

/* about explorin */
div.about-explorin h2 {
    color: var(--baseWhite);
    margin-bottom: 2.5vw;
    font-size: var(--webNormalFont3);
}

div.about-explorin p {
    font-size: var(--webSmallFont);
}

.copyright{
    font-size: var(--webSmallFont);
    font-weight: 600;
}

/* other sections */
div.explorin-other-footer-sections {
    width: 60vw;
    display: flex;
    justify-content: flex-end;
}
div.explorin-other-footer-sections  h3{
    height: 5vh;
}
/* explorin empire */
div.explorin-empire h3 {
    margin-bottom: 3vw;
}

div.explorin-empire button {
    margin-bottom: 1vw;
    color: var(--dullWhite);
    text-decoration: none;
}

button#how-to-earn-golden-ticket {
    color: var(--baseWhite);
}

/* d.e.t. */
div.explorin-det h3 {
    margin-bottom: 3vw;
}

div.explorin-det button {
    margin-bottom: 1vw;
    color: var(--dullWhite);
    text-decoration: none;
}

/* benefits */
div.explorin-benefits h3 {
    margin-bottom: 3vw;
}

div.explorin-benefits button {
    margin-bottom: 1vw;
    color: var(--dullWhite);
    text-decoration: none;
}

/* explorin-social */
div.explorin-social h3 {
    margin-bottom: 2.7vw;
    font-weight: 600;
}

div.explorin-social button {
    margin-bottom: 2.2vw;
    display: flex;
    justify-content: flex-start;
}

.fa {
    font-size: 2.5vw;
    color: var(--dullWhite);
}