.WebNavbar {
    background-color: var(--darkBlue);
    display: flex;
    justify-content: space-between;
    color: var(--baseWhite);
    padding: 0.75em;
    padding-top: 1em;
    margin-bottom: -1px;
    position: fixed;
    width: 100vw;
    z-index: 100;
    top: 0em;
    transition: ease-out 0.5s;
}

.navhide {
    top: -4em;
}

.WebNavbar__logo {
    width: 30%;
    padding-left: 1%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.WebNavbar__logo--set {
    width: 30%;
    height: calc(2em + 0.7vw);
}

img.logo {
    height: 100%;
    cursor: pointer;
}

.WebNavbar__menu {
    margin-right: 2.3%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    cursor: pointer;
    font-size: calc(0.2em + 1vw);
}

.WebNavbar__menu--items {
    margin: 0 1.2em;
    cursor: pointer;
    text-align: center;
    transition: all 0.3s;
    text-decoration: none;
    color: var(--baseWhite);
}

.WebNavbar__menu--items:hover {
    color: var(--baseOrange);
}

.btn--ticket {
    display: flex;
    border: 1px solid var(--baseWhite);
    background-color: transparent;
    border-radius: 8px;
    color: var(--baseWhite);
    padding: 0.3em 1em;
    cursor: pointer;
    font-size: calc(0.5em + 0.5vw);
    transition: all 0.5s;
}

.btn--ticket:hover {
    border: 1px solid var(--baseOrange);
    background-color: var(--baseOrange);
}

#golden-ticket-btn {
    padding: 0;
}

.btn-flip {
    height: 110%;
    flex: 1 1 12vw;
    color: var(--baseWhite);
    display: flex;
    justify-content: center;
    white-space: nowrap;
    line-height: 4vw;
    text-decoration: none;
    font-size: 1.6vw;
    opacity: 1;
    outline: 0;
    position: relative;
}

.btn-flip:hover:after {
    color: var(--baseWhite);
    opacity: 1;
    text-align: center;
    transform: translateY(0) rotateX(0);
    margin-left: 1vw;
}

.btn-flip:hover:before {
    opacity: 0;
    transform: translateY(50%) rotateX(90deg);
}

.btn-flip:after {
    top: 0;
    left: 0;
    display: block;
    transition: 0.1s;
    position: absolute;
    content: attr(data-back);
    transform: translateY(-50%) rotateX(90deg);
}

.btn-flip:before {
    top: 0;
    left: 0;
    opacity: 1;
    display: block;
    padding: 0 0;
    transition: 0.3s;
    position: relative;
    content: attr(data-front);
    transform: translateY(0) rotateX(0);
}

.btn--ticket--golden {
    border: 1px solid var(--baseOrange);
    background-color: var(--baseOrange);
    border-radius: 8px;
    color: var(--baseWhite);
    padding: 0.3em 1em;
    cursor: pointer;
    font-size: calc(0.5em + 0.5vw);
    transition: all 0.5s;
    border-radius: 8px;
}

.btn--ticket--golden:hover {
    border: 1px solid var(--baseWhite);
    background-color: transparent;
}