:root {
  /* fonts */
  --webXlHeading: calc(2em + 3.7vw);
  --webHeading: calc(0.2em + 3.1vw);
  --webHeading2: calc(0.2em + 2.5vw);
  --webNormalFont: calc(0.1em + 1.3vw);
  --webNormalFont2: calc(0.2em + 1.3vw);
  --webNormalFont3: calc(0.5em + 1.4vw);
  --webNormalFont4: calc(0.3em + 1.3vw);
  --webSmallFont: calc(0.1em + 1vw);
  --webSmallFont2: calc(0.25em + 1.2vw);
  --webSmallFont3: calc(0.1em + 0.8vw);
  --webSmallFont4: calc(0.1em + 0.9vw);
  --webSmallFont5: calc(0.1em + 1.2vw);
  --mobileHeading: calc(1.3em + 2.8vw);
  --mobileHeading2: calc(1.2em + 2.1vw);
  --mobileHeading3: calc(1.4em + 2.6vw);
  --mobileNormalFont: calc(0.6em + 1.7vw);
  --mobileMediumFont: calc(0.8em + 1.3vw);
  --mobileMediumFont2: calc(1.2em + 1.3vw);
  --mobileMediumFont3: calc(1.3em + 1.4vw);
  --mobileSmallFont: calc(0.5em + 1.2vw);
  --mobileSmallFont2: calc(0.5em + 1vw);
  --mobileSmallFont3: calc(0.4em + 1vw);

  /* colours */
  --baseOrange: #f37021;
  --shade2Orange: #f39f4d;
  --shade3Orange: #f58737;
  --shade4Orange: #ffc59a;
  --baseBlue: #172937;
  --shade2Blue: #253644;
  --shade3Blue: #2f3f4b;
  --shade4Blue: #2f4353;
  --shade5Blue: #455a64;
  --shade6Blue: #3a4f61;
  --shade7Blue: #263238;
  --shade8Blue: #8baabe;
  --lightBlue: #c4d3ea;
  --darkBlue: #030816;
  --baseWhite: #ffffff;
  --shade2White: #e5e5e5;
  --shade3White: #fafafa;
  --shade4White: #d9d9d9;
  --shade5White: #f5f7fe;
  --shade6White: #fafbff;
  --shade7White: #d0d1d3;
  --shade8White: #fcfcfc;
  --dullWhite: rgba(255, 255, 255, 0.62);
  --shade2DullWhite: rgba(255, 255, 255, 0.6);
  --baseBlack: #000000;
  --shade2Black: #413e3e;
  --shade3Black: #818080;
  --shade4Black: #9a9a9a;
  --shade5Black: #878787;
  --shade6Black: #525b62;
  --shade7Black: #212529;
  --shade1Gray: #c4c4c4;
  --baseBronze: #db8f45;
  --baseSilver: #c8c8c8;
  --shade2Golden: #776517;
  --shade3Golden: #edca74;
  --shade4Golden: #fff4d9;
}

body {
  font-family: Poppins !important;
  min-height: -webkit-fill-available;
}

.button {
  border-radius: 8px;
  color: var(--baseWhite);
  padding: 0.4em 1.1em;
  cursor: pointer;
  background-color: var(--baseOrange);
  font-size: calc(0.2em + 1.1vw);
  border: 1px solid var(--baseOrange);
  display: flex;
  align-items: center;
  margin-top: calc(0.3em + 4vw);
  margin-bottom: calc(0.1em + 1.5vw);
  transition: all 0.2s;
}

.button:hover {
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  transform: translateY(-2px);
}

.PlaneInButton {
  height: calc(1em + 0.9vw);
  width: calc(1em + 1.5vw);
  padding-left: 0.5em;
}

.temp {
  background-color: var(--baseBlue);
  width: -webkit-fill-available;
}

.democirclediv {
  height: 0;
  width: 100%;
}

.democircle {
  width: 100%;
  overflow: hidden;
  position: relative;
  border-bottom: 2.3vw dotted #f5f7ee;
  top: -1.1vw;
  margin: 0 10px;
}

a {
  text-decoration: none !important;
  color: var(--baseWhite) !important;
}

.showInMobileScreenOnly {
  display: none;
}

/* nav borders */
nav.nav.nav-tabs a.nav-item.nav-link {
  color: var(--shade6Blue);
  border: none;
  white-space: nowrap;
  font-size: 1.3vw;
  width: 0;
  margin-right: 12vw;
  margin-left: 12vw;
  display: flex;
  justify-content: center;
}

nav.nav.nav-tabs a.nav-item.nav-link.active {
  color: var(--baseWhite);
  width: 5vw;
  margin-right: 7.5vw;
  margin-left: 7.5vw;
  background: none;
  border-bottom: 5px solid var(--shade3Orange);
}

.linkedin {
  color: var(--baseWhite);
  cursor: pointer;
}

.linkedin:hover {
  color: var(--shade2Orange) !important;
}

.orange-text {
  color: var(--baseOrange);
}
