.faqs {
  background-color: var(--baseWhite);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3vw 5vw 7vw;
}

.faqs-title {
  margin: 1em 0 1em;
  font-weight: 700;
  font-style: normal;
  font-size: var(--webHeading2);
  color: var(--baseOrange);
}

.question-div {
  width: 85%;
}

.row-title {
  background: var(--shade8White);
  border: 1px solid var(--shade2White);
  padding: 1em !important;
  border-radius: 4px;
}

.row-content-text {
  padding: 1.5em 1em !important;
}

.faq-row {
  margin-top: 0.5em;
  border: none !important;
}

.icon-wrapper {
  top: 1.3em !important;
}
