div.experiences-page {
    display: flex;
    justify-content: center;
    background-color: var(--baseWhite);
}

div.experiences-page img.experiences-bg {
    width: 100%;
}

div.experiences-heading {
    position: absolute;
    margin-top: 7vw;
    font-family: 'Poppins';
    font-weight: 700;
    font-size: var(--webHeading2);
}

div.carousel-encloser {
    width: 90%;
    margin-top: 15vw;
    position: absolute;
}

div.react-multi-carousel-list.carousel-container {
    width: 100%;
	height: calc(100px + 25vw);
}

.good-card {
    margin-left: 7vw;
}

li.react-multi-carousel-item {
    display: flex;
    flex: 1;
    justify-content: center;
    width: auto;
    height: fit-content;
}