img.intro-bgimage {
    width: 100%;
}

.introbg-temp {
    background-image: linear-gradient(to right,
            #e7effa,
            #e5edf9,
            var(--shade5White),
            var(--shade5White),
            #dee3fa);
}

.intro-section {
    display: flex;
    justify-content: center;
    position: relative;
    top: 2em;
    background-color: var(--baseWhite);
}

.det-content-container {
    overflow-x: hidden;
    overflow-y: hidden;
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: calc(8.1vw);
}

.content-container {
    overflow-x: hidden;
    overflow-y: hidden;
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.what-det-heading {
    font-weight: 700;
    font-size: var(--webHeading);
}

.heading {
    font-family: 'Poppins';
    font-style: normal;
    line-height: 1.5em;
    text-align: center;
}

.what-det-desc {
    width: 47em;
    font-size: var(--webNormalFont);
    line-height: 1.5em;
    margin-top: 2.1em;
    text-align: center;
}

/* icon flex-parent */
.what-det-icons {
    margin-top: calc(3vw);
    display: flex;
    justify-content: space-between;
    width: 70vw;
}

/* icons */
.what-det-icons img {
    height: 80%;
    width: 80%;
}

/* icons subheading */
.what-det-icons .text-center {
    font-weight: 600;
    font-family: 'Poppins';
    font-size: var(--webNormalFont2);
    color: var(--shade5Blue);
    white-space: nowrap;
}

.intro-section .what-det-icons-mob {
    display: none;
}

/* icon  */
.col-3 .icon {
    height: calc(10vw);
    padding: calc(1.4vw);
    display: flex;
    justify-content: center;
}

.the-trip-content {
    margin-top: 3.5em;
}

.the-trip-heading {
    font-size: var(--webHeading2);
    font-weight: 700;
    margin: 1em 0;
}

.skills-heading {
    font-size: calc(0.2em + 1.2vw);
    text-align: left;
    font-weight: 700;
    color: var(--baseBlack);
    opacity: 0.4;
    padding-left: 3vw;
}

.career-skills {
    margin-top: calc(2vw);
}

.carousel {
    display: flex;
    flex-direction: row;
    overflow-x: hidden;
    transition: all 0.2s ease-in-out;
    cursor: pointer;
}

.carousel:hover {
    transform: scale(1.3);
}

.sliding-carousel1,
.sliding-carousel2 {
    transform: scale(1.2);
}

.sliding-carousel1 img {
    margin-left: -1.6%;
    margin-right: -1.2%;
    padding: 2em 0 3em;
    width: calc(100%);
    -webkit-animation: bannermove1 20s linear infinite;
    -moz-animation: bannermove1 20s linear infinite;
    -ms-animation: bannermove1 20s linear infinite;
    -o-animation: bannermove1 20s linear infinite;
    animation: bannermove1 20s linear infinite;
    animation-play-state: running;
}

.sliding-carousel2 img {
    margin-left: -1.6%;
    margin-right: -1.2%;
    padding: 0;
    width: calc(100%);
    -webkit-animation: bannermove2 20s linear infinite;
    -moz-animation: bannermove2 20s linear infinite;
    -ms-animation: bannermove2 20s linear infinite;
    -o-animation: bannermov2 20s linear infinite;
    animation: bannermove2 20s linear infinite;
    animation-play-state: running;
}

.carousel img.paused {
    animation-play-state: paused;
}

@keyframes bannermove1 {
    0% {
        transform: translate(-97.2%);
    }

    100% {
        transform: translate(0);
    }
}

@-moz-keyframes bannermove1 {
    0% {
        transform: translate(-97.2%);
    }

    100% {
        transform: translate(0);
    }
}

@-webkit-keyframes bannermove1 {
    0% {
        transform: translate(-97.2%);
    }

    100% {
        transform: translate(0);
    }
}

@-ms-keyframes bannermove1 {
    0% {
        transform: translate(-97.2%);
    }

    100% {
        transform: translate(0);
    }
}

@-o-keyframes bannermove1 {
    0% {
        transform: translate(-97.2%);
    }

    100% {
        transform: translate(0);
    }
}

@keyframes bannermove2 {
    100% {
        transform: translate(-97.2%);
    }

    0% {
        transform: translate(0);
    }
}

@-moz-keyframes bannermove2 {
    100% {
        transform: translate(-97.2%);
    }

    0% {
        transform: translate(0);
    }
}

@-webkit-keyframes bannermove2 {
    100% {
        transform: translate(-97.2%);
    }

    0% {
        transform: translate(0);
    }
}

@-ms-keyframes bannermove2 {
    100% {
        transform: translate(-97.2%);
    }

    0% {
        transform: translate(0);
    }
}

@-o-keyframes bannermove2 {
    100% {
        transform: translate(-97.2%);
    }

    0% {
        transform: translate(0);
    }
}