    section.learning-heading main {
        width: 70vw;
    }

    main.learning-growth section.learning-content section.learning-heading h1 {
        font-size: var(--mobileHeading);
        margin-bottom: 3vh;
    }

    main.learning-growth section.learning-content section.learning-heading p {
        width: 100%;
        font-size: var(--mobileNormalFont);
        line-height: 1.7em;
    }

    main.learning-growth section.learning-content section.learning-info {
        margin-top: 2vh;
        flex-direction: column;
        align-items: center;
    }

    section.learning-content section.learning-info div.card {
        display: flex;
        margin: 3vh 0 1vh;
        width: 80vw;
        flex-direction: row;
        justify-content: center;
        padding: calc(0.7vh + 1vw);
    }

    section.learning-info div.card div.card-captions {
        display: flex;
        align-items: center;
        font-size: 3.8vw;
        text-align: left;
        width: 50vw;
    }

    section.learning-info div.card img.icon-small {
        width: 10vw;
    }

    /* empire */
    body .BlueBg {
        width: 40vw;
    }

    div.horizontal-plane-wrapper,
    main.sliding-content div.empire-slider div.founders-side-plane-wrapper {
        display: none;
    }

    nav.nav.nav-tabs {
        flex-wrap: nowrap;
        max-width: 100%;
        overflow-x: scroll;
        overflow-y: hidden;
    }

    div#root main.empire-home-section nav.nav.nav-tabs:hover::-webkit-scrollbar {
        height: 0;
    }

    body main.empire-home-section {
        padding-top: 5vh;
    }

    nav.nav.nav-tabs::after {
        /* width: 35vw for each tab-btn; 4 btns in all */
        width: calc(35vw * 4);
    }

    div#root main.empire-home-section nav.nav.nav-tabs a.nav-item.nav-link,
    div#root main.empire-home-section nav.nav.nav-tabs a.active.nav-item.nav-link {
        width: 20vw;
        font-size: 3vw;
        display: flex;
        text-align: center;
    }

    div#root main.empire-home-section p {
        width: 85%;
    }

    div#root main.empire-home-section main.sliding-content {
        flex-direction: column;
    }

    div#root main.sliding-content div.empire-title-card {
        width: 100%;
        height: 15vh;
        font-size: 3vw;
        margin: 2vh 2vw;
    }

    div#root main.sliding-content div.empire-title-card h2 {
        font-size: 4vw;
    }

    div#root main.sliding-content div.empire-title-card {
        /* align-items: flex-start; */
        text-align: center;
        border-radius: 1vw;
    }

    div#root main.sliding-content div.empire-slider {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        height: 110vw;
        width: 90vw;
        max-width: 90vw;
        align-items: center;
    }

    div#root div.card.empire-profile-card {
        width: 40vw;
        margin: 1vh 2.5vw 1vh 2.5vw;
    }

    div#root div.card.empire-profile-card div.profile-intro-text div.name {
        font-size: 3.2vw;
    }

    div#root div.card.empire-profile-card div.profile-intro-text div.desc {
        font-size: 2.5vw;
    }

    div#root main.sliding-content div.empire-slider div.card.empire-profile-card img {
        width: 40vw;
        height: 36vw;
    }

    div#empire-tab-section-tabpane-club main.dreamerclub-flexbox {
        height: 120vw;
        flex-direction: column;
        overflow: auto;
    }

    div#root main.dreamerclub-flexbox div.empire-card-box {
        width: 45vw;
    }

    div#root main.dreamerclub-flexbox div.empire-card-box div.empire-profile-card img {
        max-width: 100%;
    }

    div#empire-tab-section-tabpane-fam main.fam-flexbox {
        height: 110vw;
        flex-direction: column;
        overflow: auto;
    }

    div#root main.fam-flexbox div.empire-card-box {
        width: 45vw;
    }

    div#root main.fam-flexbox div.empire-card-box div.empire-profile-card img {
        max-width: 100%;
    }

    div#root div#community-content div.profile-card div.identity-wrapper main.identity-content {
        font-size: 0.6em;
        min-width: 25vw;
        max-height: 1.4em;
        z-index: 2;
    }

    div#root div.profile-card div.profile div.work-logo {
        margin-bottom: 5px;
        margin-right: 5px;
        margin-top: auto;
        width: 20px;
        height: 20px;
    }

    div#root div.empire-slider div.profile-card.empire-profile-card.card div.profile div.work-logo img {
        width: 20px;
        height: 20px;
    }

    div#root div.profile-card div.profile div.work-logo[data-small-logo='true'],
    div#root div.profile-card div.profile div.work-logo[data-small-logo='true'] img {
        width: 15px;
        height: 15px;
    }

    div#root div.work-logo img.work-logo {
        width: auto;
        height: auto;
    }

    /* invisible for now because of the iPhone shadow issues */
    div#root div.shadow-wrapper {
        display: none;
        height: 110vw;
        width: 9vw;
        right: 3.6vw;
    }

    /* invisible for now because of the iPhone shadow issues */
    div#root div#nav-shadow-div {
        display: none;
        position: absolute;
        height: 8vw;
        width: 15vw;
        right: 0;
        margin-top: 10vh;
        z-index: 2;
        background-image: linear-gradient(to right,
                rgba(255, 255, 255, 0),
                var(--baseBlue));
    }

    div#root div.card.empire-profile-card div.profile div.profile-intro-text {
        max-width: inherit;
    }

    /* Predict months */
    div.predict-page {
        display: flex;
    }

    div.predict-head {
        position: relative;
    }

    img.journey-bg {
        display: none;
    }

    div.journey-heading {
        font-size: var(--mobileHeading);
        padding: 0 5vw;
        margin-top: 2em;
    }

    p.predict-title {
        font-size: var(--mobileNormalFont);
        padding: 0 5vw;
        margin: 1em auto 0;
    }

    .range-slider input[type="range"] {
        width: 80%;
        height: 2em;
    }

    .range-slider input[type="range"]::-webkit-slider-thumb {
        height: 1.8em;
        width: 1.8em;
    }

    div.range-values {
        width: 83%;
    }

    div.range-values h4 {
        font-size: var(--mobileSmallFont2);
    }

    p.range-output {
        font-size: var(--mobileMediumFont);
        padding: 0 5vw;
        text-align: center;
        margin-bottom: 5em;
    }

    /* Personlity Clubs */
    .PersonalityClubs .WhyDetData {
        position: relative;
        flex-direction: column;
    }

    .PersonalityClubs .WhyDetText {
        width: 80%;
        margin: 4em auto;
    }

    p.personalityClubsMini {
        font-size: var(--mobileSmallFont);
    }

    div.PersonalityClubs .WhyDetTitle {
        font-size: var(--mobileHeading);
        margin-bottom: 1em;
    }

    .PersonalityClubs .WhyDetIntro {
        font-size: var(--mobileNormalFont);
    }

    .PersonalityClubs .WhyDetImages {
        width: 70%;
        margin-bottom: 2em;
    }

    .PersonalityClubs .WhyDetImages img {
        width: 100%;
    }

    .PersonalityClubs button {
        width: 80%;
        margin-bottom: 3em;
    }

    /* Footer */
    body footer.page-footer {
        min-height: 90vh;
        position: relative;
        height: auto;
    }

    footer.page-footer div.explorin-other-footer-sections {
        width: 110%;
        justify-content: flex-start;
    }

    footer.page-footer div.explorin-other-footer-sections .row {
        margin-top: 2vh;
        flex-direction: column;
    }

    div.socialm {
        flex-direction: row !important;
        width: 80%;
        margin: 0 auto;
    }

    footer.page-footer main.footer-content-box div.about-explorin {
        flex: 0 0;
        width: 65vw;
        margin-left: 0;
    }

    body footer.page-footer main.footer-content-box {
        flex-direction: column;
        justify-content: flex-start;
        width: 100%;
        margin: 5vh 10vw 3vh;
        position: relative;
    }

    footer.page-footer main.footer-content-box h3 {
        font-size: var(--mobileMediumFont);
    }

    body footer.page-footer button {
        font-size: var(--mobileSmallFont);
        width: 100%;
        margin-bottom: 1.5vh;
        padding-left: 0;
    }

    footer.page-footer div.about-explorin p {
        font-size: var(--mobileSmallFont);
        margin-top: 3vh;
        line-height: 1.7em;
    }

    footer.page-footer div.about-explorin .copyright {
        margin-top: 0;
    }

    footer.page-footer .logo-foot {
        width: 60%;
    }

    footer.page-footer main.footer-content-box div.explorin-det,
    main.footer-content-box div.explorin-social {
        padding-right: 0;
    }

    body .fa {
        font-size: 6vw;
        color: var(--dullWhite);
    }
}

@media screen and (max-width: 20em) {
    body .twenem {
        display: none;
    }

    body .twenemin {
        display: block;
    }

    body .btn--ticket--golden {
        font-size: var(--mobileNormalFont);
    }
}