.empire-container {
    display: flex;
    justify-content: flex-end;
    overflow: hidden;
    flex-direction: column;
    background-color: var(--baseWhite);
}

div.empire-content .text {
    color: var(--shade2Blue);
    font-family: 'Poppins';
    font-weight: 400;
    font-size: var(--webSmallFont5);
    width: 37vw;
    margin-top: 2vw;
    margin-bottom: 3vw;
}

.empire-page {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    background: var(--baseWhite);
    position: relative;
}

.empire-content {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    padding-left: 5.5vw;
    padding-right: 40vw;
    color: var(--baseWhite);
    top: 7vw;
}

.empire-page .mobileOnly,
.mobileOnly img.empire-agent {
    display: none;
}

.empire-heading {
    font-family: 'Poppins';
    font-size: var(--webHeading2);
    font-weight: 700;
    color: var(--baseBlue);
    margin-bottom: 1vw;
    text-align: left;
    position: relative;
}

img.empire-agent {
    position: absolute;
    bottom: calc(5em + 4vw);
    height: 35vw;
    width: calc(36 * 35vw / 19);
}

img.empire-bg {
    width: 100%;
}

/* carousel for mob */
div.empire-carousel-encloser-mobile {
    display: none;
    color: black;
}

div.empire-carousel-encloser-mobile div.react-multi-carousel-list.carousel-container {
    height: 35vh;
}

div.empire-carousel-encloser-mobile ul.react-multi-carousel-dot-list {
    margin-top: 3vh;
}

div.explorin-empire-card {
    display: flex;
}

div.explorin-empire-card main.designation {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 1vw;
}

div.explorin-empire-card main strong,
div.explorin-empire-card main section {
    background: var(--lightBlue);
    font-size: 4vw;
}

div.explorin-empire-card div.profile img {
    height: 20vh;
}

div.explorin-empire-card div.profile main {
    background: var(--shade4Blue);
    color: var(--baseWhite);
    border-radius: 3px;
}

main.empire-home-section p.spacious {
    margin: 3vh 0;
}

main.empire-home-section nav.nav.nav-tabs {
    margin-top: 10vh;
}

/* empire scrollbar for flexboxes */
main.empire-home-section nav.nav.nav-tabs::-webkit-scrollbar {
    height: 0;
    width: 0;
}

main.empire-home-section nav.nav.nav-tabs:hover::-webkit-scrollbar {
    height: 10px;
}

main.empire-home-section nav.nav.nav-tabs::-webkit-scrollbar-thumb {
    background: var(--baseOrange);
    border-radius: 5px;
}

main.dreamerclub-flexbox::-webkit-scrollbar,
main.fam-flexbox::-webkit-scrollbar {
    width: 6px;
    height: 6px;
}

main.dreamerclub-flexbox:hover::-webkit-scrollbar,
main.fam-flexbox:hover::-webkit-scrollbar {
    height: 10px;
}

main.dreamerclub-flexbox::-webkit-scrollbar-track,
main.fam-flexbox::-webkit-scrollbar-track {
    border: 0;
    background: lightgrey;
    border-radius: 10px;
    margin-left: 10vh;
    margin-right: 10vh;
}

main.dreamerclub-flexbox::-webkit-scrollbar-track:hover,
main.fam-flexbox::-webkit-scrollbar-track:hover {
    background: #172937;
}

main.dreamerclub-flexbox::-webkit-scrollbar-thumb,
main.fam-flexbox::-webkit-scrollbar-thumb {
    background: #f37021;
    border-radius: 10px;
}

main.dreamerclub-flexbox::-webkit-scrollbar-thumb:hover,
main.fam-flexbox::-webkit-scrollbar-thumb:hover {
    background: #d19c7b;
}

.tripcomtxt {
    font-size: var(--webSmallFont);
    font-weight: 400;
    position: absolute;
    top: 3vw;
}