.clickIcon {
    position: fixed;
    border: 1px solid var(--baseOrange);
    border-radius: 50%;
    right: 15px;
    bottom: 15px;
    width: 70px;
    height: 70px;
    z-index: 99;
    opacity: 999;
    background-color: var(--baseOrange);
    background-image: url(../../../images/Icons/helpline.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 50px 50px;
    box-shadow: 0 13.5px 27px -5.4px rgb(50 50 93 / 25%), 0 8.1px 16.2px -8.1px rgb(0 0 0 / 30%);
    cursor: pointer;
}

#txtbox {
    min-height: 230px;
    width: 300px;
    background-color: var(--baseBlue);
    position: fixed;
    right: 31px;
    bottom: 47px;
    z-index: 97;
    border-radius: 8px;
    box-shadow: 0px 1px 61px -21px rgba(255, 255, 255, 0.75);
    -webkit-box-shadow: 0px 1px 61px -21px rgba(255, 255, 255, 0.75);
    -moz-box-shadow: 0px 1px 61px -21px rgba(255, 255, 255, 0.75);
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-size: var(--webSmallFont);
}

#inputhelp {
    height: 30%;
    width: 90%;
    resize: none;
    border: 2px solid var(--baseOrange);
    padding: 5px;
    display: flex;
    border-radius: 4px;
    overflow: hidden;
}

#inputhelp:focus-visible {
    outline-offset: 0px;
    outline: none !important;
    border: 2px solid var(--baseOrange);
}

.sendhelp {
    background-color: var(--baseOrange);
    border-radius: 8px;
    padding: 7px 17px;
    margin-top: 20px;
    display: flex;
}

.helplogo {
    height: 37px;
    width: 108px;
    background-image: url(../../../images/logo.png);
    margin-bottom: 10px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
}

.helplane {
    height: calc(1em + 0.5vw);
    width: calc(1em + 1vw);
}

.closehelp {
    position: absolute;
    right: 13px;
    top: 7px;
    cursor: pointer;
}

.closehelp .fa {
    font-size: inherit;
}